import React from 'react';
import Home from './Home';


class HomeP extends React.Component {
 state = {
    redirect: false
  }

  setRedirect = () => {
    this.setState({
      redirect: true
    })
  }


  renderRedirect = () => {
    if (this.state.redirect) {

     this.context.router.replace('/');

    }

    }

  render() {
    return (
      <Home />


    );
  }
}

export default HomeP;



