import React from 'react';


class About extends React.Component {
 state = {
    redirect: false
  }

  setRedirect = () => {
    this.setState({
      redirect: true
    })
  }


  renderRedirect = () => {
    if (this.state.redirect) {

      window.open("/about", '_blank') ;

    }

    }

  render() {
    return (
      <div  className="AboutMe">
      {this.renderRedirect()}
      <button 
       onClick={this.setRedirect}
       className="button"
      >
      About
      </button>
      </div>


    );
  }
}

export default About;



