export default class IssueThree extends React.Component {

   state = {
    redirect: false
  }

  setRedirect = () => {
    this.setState({
      redirect: true
    })
  }

  renderRedirect = () => {
    if (this.state.redirect) {
     window.location.href = 'https://drive.google.com/file/d/1hb52UD9fIuSBCvjKNdcuf0R8K7D3zKMJ/view?usp=sharing';

    }
  }

  render() {
    return (
      <div  className="IssueThree">
      {this.renderRedirect()}
      <button 
      onClick={this.setRedirect}
      className="button"
      >

      Issue Three
      </button>
        </div>

    );
  }
}

