export default class IssueTwo extends React.Component {

   state = {
    redirect: false
  }

  setRedirect = () => {
    this.setState({
      redirect: true
    })
  }

  renderRedirect = () => {
    if (this.state.redirect) {
     window.location.href = 'https://drive.google.com/file/d/0B1EZCZ5-8Od3SVA5NXh3a0NDRHc/view?usp=sharing';

    }
  }

  render() {
    return (
      <div  className="IssueTwo">
      {this.renderRedirect()}
      <button 
      onClick={this.setRedirect}
      className="button"
      >

      Issue Two
      </button>
        </div>

    );
  }
}

